import React, { Component } from 'react'
import styled from 'styled-components'
import MonthNav from 'containers/MonthNavButton'
import TitleButton from 'components/TitleButton'
import MonthSelect from 'components/MonthSelector'
import { connect } from 'react-redux'
import CurrentMonth from 'actions/CurrentMonth'
import { monthNames } from 'Data'

const Bar = styled.div`
  grid-area: header;
  display:flex;
  justify-content:space-between;
  align-content:center;
  border-bottom: ${props => props.theme.border};
  color: ${props => props.theme.mainColor};
`
const MonthYearHeading = styled.h1`
  margin: auto;
`

const MonthNavButton = MonthNav(TitleButton)

const mapStateToProps = state => {
  return {
    month: state.CurrentMonth.month,
    year: state.CurrentMonth.year
  }
}

const mapDispatchToProps = dispatch => {
  return {
    setMonth: (year, month, start) =>
      dispatch(CurrentMonth('SET', year, month, start))
  }
}

class TitleBar extends Component {
  constructor (props) {
    super(props)
    this.state = {}
  }

  handleChange = e => {
    e.preventDefault()
    let month = e.target.value
    let start = new Date(this.props.year, month, 1).getDay()
    this.props.setMonth(this.props.year, month, start)
  }

  render () {
    const month = monthNames[this.props.month]
    const year = this.props.year

    return (
      <Bar>
        <MonthNavButton />
        <MonthYearHeading>
          {month}
          {' '}
          {year}
        </MonthYearHeading>
        <MonthNavButton next={1} />
      </Bar>
    )
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(TitleBar)



