import React, { Component } from 'react'
import styled from 'styled-components'

const DayBox = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  border: ${props => props.theme.border}
  background-color: ${props => (props.date ? props.theme.backgroundColor : props.theme.nullColor)};

  & h3{
    margin: 0px 2px 0px 2px;
  }

  &:hover button {
    visibility: visible;
  };
`

const Add = styled.button`
  visibility: hidden;
  align-self: flex-end;
  margin: auto 5px 5px auto;
  background-color: ${props => props.theme.backgroundColor};
  border:1px solid lightgrey;
  border-radius: 4px;
  outline: none;
  padding: 0px;
  height: 2vh;
  width: 2vh;
  min-height: 24px;
  min-width: 24px;
  font-size: 16px;
  display: flex;
  justify-content: center;
  align-items: center;

  & *{
    margin:auto;
    display:flex;
    align-items:center;
  }

  &:hover {
    cursor: pointer;
    box-shadow: 0 5px 15px rgba(26, 26, 29, 0.3), 0 5px 15px rgba(0, 0, 0, 0.2);
  };
`

const Remove = styled.button`
  background-color: rgba(0,0,0,0);
  color: white;
  border: none;
  outline:none;
`

const EventBox = styled.div`
  display: flex;
  justify-content: space-between;
  background-color: ${props => props.color};
  color: white;
  margin:1px 0px 1px 0px;

  &:hover * {
    font-weight:700;
  }
`

const DateLabel = styled.h3`
  color: lightslategrey;
  font-size: 24px;
`

const EventText = styled.p`
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 20px;
  margin: 0px 0px 0px 2px;
`

const CalendarDay = props => {
  const { date, handleClick, reminders } = props
  const convertTime = rawTime => {
    const hours = Number(rawTime.substr(0, 2))
    if (hours === 0) {
      return 12 + rawTime.substr(2, 3) + ' AM'
    } else if (hours > 12) {
      return hours - 12 + rawTime.substr(2, 3) + ' PM'
    } else {
      return rawTime + ' AM'
    }
  }

  let displayEvents = ''
  if (reminders) {
    displayEvents = reminders.map((i, index) => (
      <EventBox
        key={i.index}
        color={i.color}
        onDoubleClick={props.handleClick(index)}
      >
        <EventText>
          {i.message}
          <br />
          {convertTime(i.time)}
        </EventText>
        <Remove onClick={props.handleRemove(index)}>-</Remove>
      </EventBox>
    ))
  }
  return (
    <DayBox date={date}>
      <DateLabel>{date || null}</DateLabel>
      {displayEvents}
      {date ? <Add onClick={handleClick()}><div><p>+</p></div></Add> : null}
    </DayBox>
  )
}

export default CalendarDay



