import React, { Component } from 'react'
import { connect } from 'react-redux'
import { modalStatus } from 'actions/index'
import ModalInput from 'components/ModalInput'
import setEvents from 'actions/CalendarEvents'

const mapStateToProps = state => {
  return {
    ...state.ModalStatus,
    events: state.CalendarEvent.data
  }
}

const mapDispatchToProps = dispatch => {
  return {
    setModal: () => dispatch(modalStatus('MODAL_OFF')),
    setEvent: (type, year, month, day, message, time, color, index) =>
      dispatch(setEvents(type, year, month, day, message, time, color, index))
  }
}

class ModalContainer extends Component {
  constructor (props) {
    super(props)
    const dayEvents = props.events[`${props.year}_${props.month}_${props.date}`]
    console.log('index', props.index)
    const event = dayEvents
      ? dayEvents[props.index] || { message: '', time: '00:00', color: 'black' }
      : { message: '', time: '00:00', color: 'black' }
    const formattedDate = this.formatDate(props.year, props.month, props.date)
    this.state = {
      ...event,
      error: false,
      date: formattedDate
    }
  }

  formatDate = (year, month, date) => {
    let formattedMonth = month + 1 > 10 ? month + 1 + '' : '0' + (month + 1)
    let formattedDate = date > 10 ? date + '' : '0' + date
    return year + '-' + formattedMonth + '-' + formattedDate
  }

  unformatDate = formattedDate => {
    return {
      year: Number(formattedDate.substr(0, 4)),
      month: Number(formattedDate.substr(5, 2)) - 1,
      date: Number(formattedDate.substr(8, 2))
    }
  }

  handleChange = type => e => {
    e.preventDefault()
    let newState = {}
    newState[type] = e.target.value
    if (type === 'message') {
      if (!this.state.error && newState[type].length > 30) {
        newState.error = true
      } else if (this.state.error && newState[type].length <= 30) {
        newState.error = false
      }
    }
    this.setState(newState)
  }

  handleSubmit = e => {
    e.preventDefault()
    let type = ''
    const date = this.unformatDate(this.state.date)
    // edit is fundamentally flawed, need to make edit -> remove and then set
    if (this.props.index >= 0) {
      this.props.setEvent(
        'REMOVE_EVENT',
        this.props.year,
        this.props.month,
        this.props.date,
        this.state.message,
        this.state.time,
        this.state.color,
        this.props.index
      )
    }
    type = 'SET_EVENT'

    if (!this.state.error) {
      this.props.setEvent(
        type,
        date.year,
        date.month,
        date.date,
        this.state.message,
        this.state.time,
        this.state.color,
        this.props.index
      )
      this.props.setModal()
      this.setState({ error: false })
    }
  }

  render () {
    return (
      <ModalInput
        toggleModal={this.props.setModal}
        handleChangeMessage={this.handleChange('message')}
        handleChangeTime={this.handleChange('time')}
        handleChangeColor={this.handleChange('color')}
        handleChangeDate={this.handleChange('date')}
        message={this.state.message}
        handleSubmit={this.handleSubmit}
        error={this.state.error}
        time={this.state.time}
        color={this.state.color}
        date={this.state.date}
      />
    )
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ModalContainer)



