import React from 'react'
import styled, { keyframes } from 'styled-components'
import { colors } from 'Data'

const fadeIn = keyframes`
  0% {
    background: rgba(0, 0, 0, .0);
  }
100 % {
  background: rgba(0, 0, 0, .7);
}
`

const ModalPage = styled.div`
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.63);
  position: fixed;
  z-index: 10;
  display:flex;
  justify-content: center;
  align-items: center;

  animation: ${fadeIn} .5s cubic-bezier(0.165, 0.840, 0.440, 1.000) forwards;
`

const slideIn = keyframes`
  from {
    margin-bottom: 100%;
  }

  to {
    margin:bottom auto;
  }
`

const FormContainer = styled.div`
  background: white;
  height: 25%;
  width: 25%;
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 20;
  border-radius: 5px;

  animation: ${slideIn} .5s cubic-bezier(0.165, 0.840, 0.440, 1.000) forwards;
  
`

const DivBackground = styled.div`
  position:fixed;
  height: 100%;
  width: 100%;
  z-index:15;
`

const InputForm = styled.input`
  border: 1px solid ${props => (props.error ? 'red' : 'black')};
  outline:none;
`

const ColorSelect = styled.select`
  background-color: ${props => props.color};
`

const ColorOption = styled.option`
  background-color: ${props => props.color};
  &:checked {
    box-shadow: 0 0 10px 100px rgba(0,0,0,0) inset;
  }
`

const ModalInput = props => {
  const options = colors.map(color => (
    <ColorOption key={color.name} value={color.color} color={color.color} />
  ))

  return (
    <ModalPage>
      <DivBackground onClick={props.toggleModal} />
      <FormContainer>
        <input
          type='date'
          value={props.date}
          onChange={props.handleChangeDate}
        />
        <form onSubmit={props.handleSubmit}>
          <ColorSelect
            value={props.color}
            onChange={props.handleChangeColor}
            color={props.color}
          >
            {options}
          </ColorSelect>
          <InputForm
            type='text'
            value={props.message}
            onChange={props.handleChangeMessage}
            error={props.error}
          />
          <input
            type='time'
            value={props.time}
            onChange={props.handleChangeTime}
          />
        </form>
      </FormContainer>

    </ModalPage>
  )
}

export default ModalInput



