import React, { Component } from 'react'
import { connect } from 'react-redux'
import styled from 'styled-components'
import Day from 'containers/DayContainer'
import { monthDays } from 'Data'

const Grid = styled.div`
  height:100%;
  grid-area: body;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-auto-rows: 1fr;
`

const mapStateToProps = state => {
  return {
    month: state.CurrentMonth.month,
    start: state.CurrentMonth.start,
    year: state.CurrentMonth.year
  }
}

class CalendarBody extends Component {
  populateSquares = () => {
    const paddingBefore = this.props.start
    const paddingAfter =
      (7 - (monthDays[this.props.month] + this.props.start) % 7) % 7
    const items = []
    for (let i = 0; i < paddingBefore; i++) {
      items.push(<Day date={0} key={items.length + 1} />)
    }
    for (let i = 1; i <= monthDays[this.props.month]; i++) {
      items.push(
        <Day
          date={i}
          month={this.props.month}
          year={this.props.year}
          key={items.length + 1}
        />
      )
    }
    for (let i = 0; i < paddingAfter; i++) {
      items.push(<Day date={0} key={items.length + 1} />)
    }
    return items
  }

  render () {
    return (
      <Grid>
        {this.populateSquares()}
      </Grid>
    )
  }
}

const ConnectedCalendarBody = connect(mapStateToProps)(CalendarBody)

export default ConnectedCalendarBody



